import React from 'react';

function About() {         // 全用函数组件
    return(
        <>                
            <h1>介绍：该项目是一个图床，用户注册登录后可上传自己的图片，也可使用图片</h1>
            <h5>使用方法，注册登录后，可在上传历史中找到自己想要的图片链接</h5>
        </>
    );                   // 在组件render中，必须用一个标签包裹起来，即使是空标签
}


export default About;